"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface Column {
  key: string;
  label: string;
  type: 'text' | 'number' | 'date' | 'select' | 'badge' | 'boolean';
  editable?: boolean;
  options?: string[];
  width?: string;
}

interface ExcelGridProps {
  data: any[];
  columns: Column[];
  onDataChange?: (data: any[]) => void;
  onRowSelect?: (selectedRows: any[]) => void;
  title?: string;
  searchable?: boolean;
  filterable?: boolean;
  exportable?: boolean;
  addNewRow?: boolean;
}

export default function ExcelGrid({
  data,
  columns,
  onDataChange,
  onRowSelect,
  title = "Data Grid",
  searchable = true,
  filterable = true,
  exportable = true,
  addNewRow = true
}: ExcelGridProps) {
  const [gridData, setGridData] = useState(data);
  const [selectedRows, setSelectedRows] = useState<number[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [sortColumn, setSortColumn] = useState<string>("");
  const [sortDirection, setSortDirection] = useState<"asc" | "desc">("asc");
  const [filters, setFilters] = useState<Record<string, string>>({});
  const [editingCell, setEditingCell] = useState<{row: number, col: string} | null>(null);
  const [duplicateAlert, setDuplicateAlert] = useState<string>("");

  useEffect(() => {
    setGridData(data);
  }, [data]);

  // Filter and search logic
  const filteredData = gridData.filter(row => {
    // Search filter
    if (searchTerm) {
      const searchMatch = columns.some(col => 
        String(row[col.key]).toLowerCase().includes(searchTerm.toLowerCase())
      );
      if (!searchMatch) return false;
    }

    // Column filters
    for (const [key, value] of Object.entries(filters)) {
      if (value && String(row[key]) !== value) {
        return false;
      }
    }

    return true;
  });

  // Sort logic
  const sortedData = [...filteredData].sort((a, b) => {
    if (!sortColumn) return 0;
    
    const aVal = a[sortColumn];
    const bVal = b[sortColumn];
    
    if (aVal < bVal) return sortDirection === "asc" ? -1 : 1;
    if (aVal > bVal) return sortDirection === "asc" ? 1 : -1;
    return 0;
  });

  const handleSort = (columnKey: string) => {
    if (sortColumn === columnKey) {
      setSortDirection(sortDirection === "asc" ? "desc" : "asc");
    } else {
      setSortColumn(columnKey);
      setSortDirection("asc");
    }
  };

  const handleCellEdit = (rowIndex: number, columnKey: string, value: any) => {
    const newData = [...gridData];
    const actualRowIndex = gridData.findIndex(row => row === sortedData[rowIndex]);
    
    // Duplicate check
    if (columnKey === 'id' || columnKey === 'sku' || columnKey === 'code') {
      const isDuplicate = gridData.some((row, index) => 
        index !== actualRowIndex && row[columnKey] === value
      );
      
      if (isDuplicate) {
        setDuplicateAlert(`Duplicate ${columnKey}: ${value} already exists!`);
        setTimeout(() => setDuplicateAlert(""), 3000);
        return;
      }
    }
    
    newData[actualRowIndex] = { ...newData[actualRowIndex], [columnKey]: value };
    setGridData(newData);
    onDataChange?.(newData);
    setEditingCell(null);
  };

  const handleRowSelect = (rowIndex: number) => {
    const newSelected = selectedRows.includes(rowIndex)
      ? selectedRows.filter(i => i !== rowIndex)
      : [...selectedRows, rowIndex];
    
    setSelectedRows(newSelected);
    onRowSelect?.(newSelected.map(i => sortedData[i]));
  };

  const handleSelectAll = () => {
    if (selectedRows.length === sortedData.length) {
      setSelectedRows([]);
      onRowSelect?.([]);
    } else {
      const allIndices = sortedData.map((_, index) => index);
      setSelectedRows(allIndices);
      onRowSelect?.(sortedData);
    }
  };

  const addNewRowHandler = () => {
    const newRow = columns.reduce((acc, col) => {
      acc[col.key] = col.type === 'number' ? 0 : col.type === 'boolean' ? false : '';
      return acc;
    }, {} as any);
    
    const newData = [...gridData, { ...newRow, id: Date.now() }];
    setGridData(newData);
    onDataChange?.(newData);
  };

  const exportToCSV = () => {
    const headers = columns.map(col => col.label).join(',');
    const rows = sortedData.map(row => 
      columns.map(col => `"${row[col.key]}"`).join(',')
    ).join('\n');
    
    const csv = `${headers}\n${rows}`;
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `${title.replace(/\s+/g, '_')}.csv`;
    a.click();
  };

  const renderCell = (row: any, column: Column, rowIndex: number) => {
    const isEditing = editingCell?.row === rowIndex && editingCell?.col === column.key;
    const value = row[column.key];

    if (isEditing && column.editable) {
      if (column.type === 'select') {
        return (
          <Select
            value={value}
            onValueChange={(newValue) => handleCellEdit(rowIndex, column.key, newValue)}
          >
            <SelectTrigger className="h-8">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {column.options?.map(option => (
                <SelectItem key={option} value={option}>{option}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        );
      } else {
        return (
          <Input
            type={column.type === 'number' ? 'number' : 'text'}
            value={value}
            onChange={(e) => handleCellEdit(rowIndex, column.key, e.target.value)}
            onBlur={() => setEditingCell(null)}
            onKeyDown={(e) => {
              if (e.key === 'Enter') setEditingCell(null);
              if (e.key === 'Escape') setEditingCell(null);
            }}
            className="h-8"
            autoFocus
          />
        );
      }
    }

    // Display cell content
    if (column.type === 'badge') {
      const variant = value === 'Active' || value === 'In Stock' || value === 'Paid' ? 'default' :
                    value === 'Inactive' || value === 'Out of Stock' || value === 'Pending' ? 'secondary' :
                    'destructive';
      return <Badge variant={variant}>{value}</Badge>;
    }

    if (column.type === 'boolean') {
      return <Checkbox checked={value} disabled />;
    }

    if (column.type === 'number' && typeof value === 'number') {
      return value.toLocaleString();
    }

    return (
      <div
        className={column.editable ? "cursor-pointer hover:bg-gray-50 p-1 rounded" : ""}
        onClick={() => column.editable && setEditingCell({row: rowIndex, col: column.key})}
      >
        {value}
      </div>
    );
  };

  return (
    <div className="space-y-4">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <h2 className="text-xl font-semibold">{title}</h2>
        <div className="flex gap-2">
          {addNewRow && (
            <Button onClick={addNewRowHandler} size="sm">
              Add New Row
            </Button>
          )}
          {exportable && (
            <Button onClick={exportToCSV} variant="outline" size="sm">
              Export CSV
            </Button>
          )}
        </div>
      </div>

      {/* Duplicate Alert */}
      {duplicateAlert && (
        <Alert className="border-red-200 bg-red-50">
          <AlertDescription className="text-red-800">
            {duplicateAlert}
          </AlertDescription>
        </Alert>
      )}

      {/* Search and Filters */}
      <div className="flex flex-col sm:flex-row gap-4">
        {searchable && (
          <div className="flex-1">
            <Input
              placeholder="Search across all columns..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="max-w-sm"
            />
          </div>
        )}
        
        {filterable && (
          <div className="flex gap-2 flex-wrap">
            {columns.filter(col => col.type === 'select' || col.type === 'badge').map(col => (
              <Select
                key={col.key}
                value={filters[col.key] || ""}
                onValueChange={(value) => setFilters({...filters, [col.key]: value})}
              >
                <SelectTrigger className="w-40">
                  <SelectValue placeholder={`Filter ${col.label}`} />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All {col.label}</SelectItem>
                  {col.options?.map(option => (
                    <SelectItem key={option} value={option}>{option}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            ))}
          </div>
        )}
      </div>

      {/* Data Table */}
      <div className="border rounded-lg overflow-hidden">
        <div className="overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead className="w-12">
                  <Checkbox
                    checked={selectedRows.length === sortedData.length && sortedData.length > 0}
                    onCheckedChange={handleSelectAll}
                  />
                </TableHead>
                {columns.map(column => (
                  <TableHead
                    key={column.key}
                    className={`cursor-pointer hover:bg-gray-50 ${column.width || ''}`}
                    onClick={() => handleSort(column.key)}
                  >
                    <div className="flex items-center gap-2">
                      {column.label}
                      {sortColumn === column.key && (
                        <span className="text-xs">
                          {sortDirection === "asc" ? "↑" : "↓"}
                        </span>
                      )}
                    </div>
                  </TableHead>
                ))}
              </TableRow>
            </TableHeader>
            <TableBody>
              {sortedData.map((row, index) => (
                <TableRow key={row.id || index} className="hover:bg-gray-50">
                  <TableCell>
                    <Checkbox
                      checked={selectedRows.includes(index)}
                      onCheckedChange={() => handleRowSelect(index)}
                    />
                  </TableCell>
                  {columns.map(column => (
                    <TableCell key={column.key} className={column.width || ''}>
                      {renderCell(row, column, index)}
                    </TableCell>
                  ))}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </div>

      {/* Footer */}
      <div className="flex justify-between items-center text-sm text-gray-600">
        <span>
          Showing {sortedData.length} of {gridData.length} rows
          {selectedRows.length > 0 && ` • ${selectedRows.length} selected`}
        </span>
        <span>
          Use keyboard shortcuts: Enter to save, Escape to cancel
        </span>
      </div>
    </div>
  );
}
