"use client";

import { useState } from "react";
import ExcelGrid from "@/components/ExcelGrid";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Progress } from "@/components/ui/progress";

// Mock inventory data
const mockInventoryData = [
  {
    id: 1,
    sku: "SKU001",
    name: "iPhone 15 Pro Max",
    category: "Electronics",
    price: 134900,
    stock: 25,
    minStock: 10,
    maxStock: 100,
    supplier: "Apple Inc",
    status: "In Stock",
    lastUpdated: "2024-01-15",
    barcode: "1234567890123",
    location: "A1-B2-C3"
  },
  {
    id: 2,
    sku: "SKU002",
    name: "Samsung Galaxy S24",
    category: "Electronics",
    price: 89999,
    stock: 5,
    minStock: 10,
    maxStock: 50,
    supplier: "Samsung",
    status: "Low Stock",
    lastUpdated: "2024-01-14",
    barcode: "2345678901234",
    location: "A1-B3-C1"
  },
  {
    id: 3,
    sku: "SKU003",
    name: "MacBook Air M3",
    category: "Computers",
    price: 114900,
    stock: 0,
    minStock: 5,
    maxStock: 25,
    supplier: "Apple Inc",
    status: "Out of Stock",
    lastUpdated: "2024-01-13",
    barcode: "3456789012345",
    location: "B1-A2-C4"
  },
  {
    id: 4,
    sku: "SKU004",
    name: "Dell XPS 13",
    category: "Computers",
    price: 95000,
    stock: 15,
    minStock: 8,
    maxStock: 30,
    supplier: "Dell Technologies",
    status: "In Stock",
    lastUpdated: "2024-01-15",
    barcode: "4567890123456",
    location: "B1-A3-C2"
  },
  {
    id: 5,
    sku: "SKU005",
    name: "AirPods Pro 2",
    category: "Accessories",
    price: 24900,
    stock: 45,
    minStock: 20,
    maxStock: 100,
    supplier: "Apple Inc",
    status: "In Stock",
    lastUpdated: "2024-01-15",
    barcode: "5678901234567",
    location: "C1-A1-B2"
  }
];

const inventoryColumns = [
  { key: 'sku', label: 'SKU', type: 'text' as const, editable: true, width: 'w-32' },
  { key: 'name', label: 'Product Name', type: 'text' as const, editable: true, width: 'w-48' },
  { key: 'category', label: 'Category', type: 'select' as const, editable: true, options: ['Electronics', 'Computers', 'Accessories', 'Clothing', 'Home'], width: 'w-32' },
  { key: 'price', label: 'Price (₹)', type: 'number' as const, editable: true, width: 'w-32' },
  { key: 'stock', label: 'Current Stock', type: 'number' as const, editable: true, width: 'w-32' },
  { key: 'minStock', label: 'Min Stock', type: 'number' as const, editable: true, width: 'w-28' },
  { key: 'maxStock', label: 'Max Stock', type: 'number' as const, editable: true, width: 'w-28' },
  { key: 'supplier', label: 'Supplier', type: 'text' as const, editable: true, width: 'w-40' },
  { key: 'status', label: 'Status', type: 'badge' as const, options: ['In Stock', 'Low Stock', 'Out of Stock'], width: 'w-32' },
  { key: 'location', label: 'Location', type: 'text' as const, editable: true, width: 'w-32' },
  { key: 'barcode', label: 'Barcode', type: 'text' as const, editable: true, width: 'w-40' },
  { key: 'lastUpdated', label: 'Last Updated', type: 'date' as const, width: 'w-32' }
];

export default function InventoryPage() {
  const [inventoryData, setInventoryData] = useState(mockInventoryData);
  const [selectedProducts, setSelectedProducts] = useState<any[]>([]);
  const [showLowStockOnly, setShowLowStockOnly] = useState(false);

  // Calculate inventory stats
  const totalProducts = inventoryData.length;
  const inStock = inventoryData.filter(item => item.status === 'In Stock').length;
  const lowStock = inventoryData.filter(item => item.status === 'Low Stock').length;
  const outOfStock = inventoryData.filter(item => item.status === 'Out of Stock').length;
  const totalValue = inventoryData.reduce((sum, item) => sum + (item.price * item.stock), 0);

  const handleDataChange = (newData: any[]) => {
    // Update status based on stock levels
    const updatedData = newData.map(item => ({
      ...item,
      status: item.stock === 0 ? 'Out of Stock' : 
              item.stock <= item.minStock ? 'Low Stock' : 'In Stock',
      lastUpdated: new Date().toISOString().split('T')[0]
    }));
    setInventoryData(updatedData);
  };

  const handleRowSelect = (selected: any[]) => {
    setSelectedProducts(selected);
  };

  const generateBarcode = () => {
    if (selectedProducts.length === 0) {
      alert("Please select products to generate barcodes");
      return;
    }
    
    // Mock barcode generation
    alert(`Generating barcodes for ${selectedProducts.length} selected products`);
  };

  const generateQRCode = () => {
    if (selectedProducts.length === 0) {
      alert("Please select products to generate QR codes");
      return;
    }
    
    // Mock QR code generation
    alert(`Generating QR codes for ${selectedProducts.length} selected products`);
  };

  const triggerReorder = () => {
    const lowStockItems = inventoryData.filter(item => item.stock <= item.minStock);
    if (lowStockItems.length === 0) {
      alert("No items need reordering at this time");
      return;
    }
    
    alert(`Auto-reorder triggered for ${lowStockItems.length} items`);
  };

  const exportInventoryReport = () => {
    // Mock export functionality
    alert("Inventory report exported successfully");
  };

  const filteredData = showLowStockOnly 
    ? inventoryData.filter(item => item.status === 'Low Stock' || item.status === 'Out of Stock')
    : inventoryData;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Inventory Management</h1>
          <p className="text-gray-600">Manage your product inventory with Excel-like controls</p>
        </div>
        
        <div className="flex gap-2">
          <Button onClick={generateBarcode} variant="outline" size="sm">
            Generate Barcodes
          </Button>
          <Button onClick={generateQRCode} variant="outline" size="sm">
            Generate QR Codes
          </Button>
          <Button onClick={triggerReorder} size="sm">
            Auto Reorder
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Products</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{totalProducts}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">In Stock</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{inStock}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Low Stock</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{lowStock}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Out of Stock</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{outOfStock}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Value</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">₹{totalValue.toLocaleString()}</div>
          </CardContent>
        </Card>
      </div>

      {/* Alerts */}
      {lowStock > 0 && (
        <Alert className="border-orange-200 bg-orange-50">
          <AlertDescription className="text-orange-800">
            <strong>{lowStock} products</strong> are running low on stock and need attention.
            <Button 
              onClick={() => setShowLowStockOnly(!showLowStockOnly)} 
              variant="link" 
              className="ml-2 p-0 h-auto text-orange-800 underline"
            >
              {showLowStockOnly ? "Show all products" : "View low stock items"}
            </Button>
          </AlertDescription>
        </Alert>
      )}

      {outOfStock > 0 && (
        <Alert className="border-red-200 bg-red-50">
          <AlertDescription className="text-red-800">
            <strong>{outOfStock} products</strong> are completely out of stock and require immediate restocking.
          </AlertDescription>
        </Alert>
      )}

      {/* Inventory Health */}
      <Card>
        <CardHeader>
          <CardTitle>Inventory Health</CardTitle>
          <CardDescription>Overall inventory status and performance metrics</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <div className="flex justify-between text-sm mb-2">
              <span>Stock Availability</span>
              <span>{Math.round((inStock / totalProducts) * 100)}%</span>
            </div>
            <Progress value={(inStock / totalProducts) * 100} className="h-2" />
          </div>
          
          <div>
            <div className="flex justify-between text-sm mb-2">
              <span>Inventory Turnover</span>
              <span>78%</span>
            </div>
            <Progress value={78} className="h-2" />
          </div>
          
          <div className="grid grid-cols-3 gap-4 mt-4">
            <div className="text-center">
              <div className="text-lg font-semibold text-green-600">{inStock}</div>
              <div className="text-xs text-gray-600">Available</div>
            </div>
            <div className="text-center">
              <div className="text-lg font-semibold text-orange-600">{lowStock}</div>
              <div className="text-xs text-gray-600">Low Stock</div>
            </div>
            <div className="text-center">
              <div className="text-lg font-semibold text-red-600">{outOfStock}</div>
              <div className="text-xs text-gray-600">Out of Stock</div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Excel Grid */}
      <Card>
        <CardContent className="p-6">
          <ExcelGrid
            data={filteredData}
            columns={inventoryColumns}
            onDataChange={handleDataChange}
            onRowSelect={handleRowSelect}
            title="Product Inventory"
            searchable={true}
            filterable={true}
            exportable={true}
            addNewRow={true}
          />
        </CardContent>
      </Card>

      {/* Quick Actions */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Actions</CardTitle>
          <CardDescription>Frequently used inventory operations</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <Button variant="outline" className="h-20 flex-col">
              <span className="font-semibold">Bulk Import</span>
              <span className="text-xs text-gray-600">CSV/Excel</span>
            </Button>
            <Button variant="outline" className="h-20 flex-col" onClick={exportInventoryReport}>
              <span className="font-semibold">Export Report</span>
              <span className="text-xs text-gray-600">PDF/Excel</span>
            </Button>
            <Button variant="outline" className="h-20 flex-col">
              <span className="font-semibold">Stock Audit</span>
              <span className="text-xs text-gray-600">Physical Count</span>
            </Button>
            <Button variant="outline" className="h-20 flex-col">
              <span className="font-semibold">Price Update</span>
              <span className="text-xs text-gray-600">Bulk Pricing</span>
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
